.ldshourglass {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .ldshourglass:after {
    content: " ";
    display: block;
    border-radius: 50%;
    width: 0;
    height: 0;
    margin: 8px;
    box-sizing: border-box;
    border: 32px solid rgb(218, 69, 69);
    border-color:  rgb(218, 69, 69) transparent  rgb(218, 69, 69) transparent;
    animation: lds-hourglass 1.2s infinite;
  }
  @keyframes lds-hourglass {
    0% {
      transform: rotate(0);
      animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    50% {
      transform: rotate(900deg);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    100% {
      transform: rotate(1800deg);
    }
  }
.wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 400px;
}